.cell {
  width: 10%;
  height: 10%;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
}

.cell:hover {
  cursor: pointer;
  -webkit-filter: brightness(110%);
          filter: brightness(110%);
}

.cell::after {
  position: absolute;
  width: 100%;
  height: 100%;
  background: lightskyblue;
  display: block;
  content: '';
}

.open::after{
  display: none;
}

.mine {
  background: red;
}

.maybe-mine::after {
  content: '?';
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}


